import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormControl, FormArray, FormGroup, Validators } from '@angular/forms';
/* Services */
import { AccountService } from '../services/account.service';
import { OperatorType } from '../interfaces/operator-type';
import { ServiceType } from '../interfaces/service-type';
import { Vehicle } from '../interfaces/vehicle';
/* Dialog */
import { MatDialog, MatDialogConfig, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { AddVehicleComponent } from '../messages/add-vehicle/add-vehicle.component';
import { RegisterSuccessComponent } from '../messages/register-success/register-success.component';

@Component({
  selector: 'account-register',
  templateUrl: './account-register.component.html',
  styleUrls: ['./account-register.component.scss']
})
export class AccountRegisterComponent implements OnInit {

  accountRegisterForm;
  vehicleInformation: Vehicle;
  selectedServiceType: string;
  selectedOperatorType: string;
  serviceTypes: ServiceType[] = [
    { code: '1', name: 'Transport_Service', description: 'Transport Service' },
    { code: '2', name: 'Collector_Service', description: 'Collector Service' },
  ];
  operatorTypes: OperatorType[] = [
    { code: '1', name: 'Transport', description: 'Transport type' },
    { code: '2', name: 'Collector', description: 'Collector type' },
  ];

  constructor(private formBuilder: FormBuilder, private accountService: AccountService, public matDialog: MatDialog) {
    this.accountRegisterForm = this.formBuilder.group({
      accountSearch: new FormControl('', [
        /*Validators.required*/
      ]),
      account: new FormControl({value: '', disabled: true}, [
        /*Validators.required*/
      ]),
      user: new FormControl({value: '', disabled: true}, [

      ]),
      description: new FormControl('', [

      ]),
      status: new FormControl(false, [

      ]),
      serviceType: new FormControl('', [

      ]),
      matrix: this.formBuilder.group({
        identifier: new FormControl('', [

        ]),
        name: new FormControl('', [

        ]),
        matrixDescription: new FormControl('', [

        ])
      }),
      route: this.formBuilder.group({
        routeIdentifier: new FormControl('', [

        ]),
        routeName: new FormControl('', [

        ]),
        routeDescription: new FormControl('', [

        ]),
        routeStatus: new FormControl(true, [

        ]),
      }),
      operator: this.formBuilder.group({
        operatorCode: new FormControl('', [

        ]),
        operatorName: new FormControl('', [

        ]),
        operatorLastName: new FormControl('', [

        ]),
        operatorType: new FormControl('', [

        ]),
        operatorIdentification: new FormControl('', [

        ]),
        operatorDateAdmission: new FormControl('', [

        ])
      }),
    })
  }

  ngOnInit() {
  }

  /* ---------- Load Information ----- */
  searchAccount(userAccount) {
    this.accountService.getAccountsByUserAccount(userAccount)
    .subscribe(data => {
      this.accountRegisterForm.patchValue({
        account: data[0].account,
        user: data[0].username,
      });
      console.log("Get account success! " );
    });
  }

  addVehicle(): void {
    this.openAddVehicle();
    /*this.cameras = this.accountRegisterForm.get('cameras') as FormArray;
    this.cameras.push(this.createCamera());*/
  }

  /* ---------- Save ----------------- */
  registerAccount() {
    //TEST
    /*console.log(this.accountRegisterForm.value);
    console.log("raw value");
    console.log(this.accountRegisterForm.getRawValue());


    this.openSuccesRegister("12345");*/
    const accountForm = this.accountRegisterForm.getRawValue();
    accountForm.operator.vehicle = this.vehicleInformation;
    this.accountService.registerAccount(accountForm)
    .subscribe(data => {

      this.openSuccesRegister(this.accountRegisterForm.getRawValue().account);
      this.accountRegisterForm.reset();
      console.log("Get account success! " );
    });
  }

  cancelRegisterAccount() {
    this.accountRegisterForm.reset();
  }

  /* ---------- Dialog --------------- */
  openSuccesRegister(message) {
    const session = JSON.parse(localStorage.getItem('session'));
    const dialogConfig = new MatDialogConfig();
    // The user can't close the dialog by clicking outside its body
    dialogConfig.disableClose = true;
    dialogConfig.id = "register-success-message";
    dialogConfig.height = "20em";
    dialogConfig.width = "30em";
    dialogConfig.data = {title: session.clientId, message: message};
    // https://material.angular.io/components/dialog/overview
    const modalDialog = this.matDialog.open(RegisterSuccessComponent, dialogConfig);
  }

  openAddVehicle() {
    const dialogVehicleConfig = new MatDialogConfig();
    // The user can't close the dialog by clicking outside its body
    dialogVehicleConfig.disableClose = true;
    dialogVehicleConfig.id = "add-vehicle";
    dialogVehicleConfig.height = "30em";
    dialogVehicleConfig.width = "50em";
    dialogVehicleConfig.data = {};
    // https://material.angular.io/components/dialog/overview
    const modalVehicleDialog = this.matDialog.open(AddVehicleComponent, dialogVehicleConfig);

    modalVehicleDialog.afterClosed().subscribe(data => {
      console.log('The dialog was closed');
      console.log(data);
      if (data != undefined) {
        this.vehicleInformation = data
      }
    });
  }

  /* ----------  Form attributes ----- */
  get accountSearch() { return this.accountRegisterForm.get('accountSearch'); }
  get account() { return this.accountRegisterForm.get('account'); }
  get user() { return this.accountRegisterForm.get('user'); }
  get description() { return this.accountRegisterForm.get('description'); }
  get status() { return this.accountRegisterForm.get('status'); }
  get serviceType() { return this.accountRegisterForm.get('serviceType'); }
  get identifier() { return this.accountRegisterForm.get('matrix').get('identifier'); }
  get name() { return this.accountRegisterForm.get('matrix').get('name'); }
  get matrixDescription() { return this.accountRegisterForm.get('matrix').get('matrixDescription'); }
  get routeIdentifier() { return this.accountRegisterForm.get('route').get('routeIdentifier'); }
  get routeName() { return this.accountRegisterForm.get('route').get('routeName'); }
  get routeDescription() { return this.accountRegisterForm.get('route').get('routeDescription'); }
  get operatorCode() { return this.accountRegisterForm.get('operator').get('operatorCode'); }
  get operatorName() { return this.accountRegisterForm.get('operator').get('operatorName'); }
  get operatorLastName() { return this.accountRegisterForm.get('operator').get('operatorLastName'); }
  get operatorType() { return this.accountRegisterForm.get('operator').get('operatorType'); }
  get operatorDateAdmission() { return this.accountRegisterForm.get('operator').get('operatorDateAdmission'); }

}
