import { Component, OnInit, AfterViewInit, ViewEncapsulation } from '@angular/core';
import { MatBottomSheet, MatBottomSheetRef } from '@angular/material/bottom-sheet';
import { AccountService } from '../services/account.service';
import { GeolocalizationService } from '../services/geolocalization.service';
import { TrackAccountsSearchComponent } from '../messages/track-accounts-search/track-accounts-search.component';

@Component({
  selector: 'track-accounts',
  templateUrl: './track-accounts.component.html',
  styleUrls: ['./track-accounts.component.scss'],
  encapsulation: ViewEncapsulation.None
})
export class TrackAccountsComponent implements AfterViewInit {
  disableSelectAccounts = true;
  accounts;
  vehicles;

  constructor(private _bottomSheet: MatBottomSheet,
              private accountService: AccountService,
              private geolocalizationService: GeolocalizationService) { }

  ngOnInit() {  }

  ngAfterViewInit() {
    this.loadAccounts();
  }

  openAccountSearch(): void {
    this._bottomSheet.open(TrackAccountsSearchComponent, {
      data: { accounts: this.accounts }, disableClose: true
    }).afterDismissed().subscribe(data => {
      this.loadCheckpoints(data);
      console.log('Data =' + data);
    });
  }

  loadAccounts() {
    this.accountService.getAccountsDetails()
    .subscribe(data => {
      this.accounts = data;
      this.openAccountSearch();
      console.log("Load Accounts success! " );
    });
  }

  loadCheckpoints(route) {
    this.geolocalizationService.getCheckpointsByRoute(route)
    .subscribe(data => {
      this.vehicles = data;
      console.log("Load Checkpoints success! " );
    })
  }

}
