import { Component, OnInit, AfterViewInit, ViewEncapsulation } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { GeolocalizationService } from '../services/geolocalization.service';
import { CheckpointAccount } from './../interfaces/checkpoint-account';
import { CryptoTool } from '../../core/crypto/crypto-tool'

@Component({
  selector: 'track-account',
  templateUrl: './track-account.component.html',
  styleUrls: ['./track-account.component.scss'],
  encapsulation: ViewEncapsulation.None
})
export class TrackAccountComponent implements AfterViewInit {

  vehicles;
  checkpoints: CheckpointAccount[] = [];

  constructor(private route: ActivatedRoute, private geolocalizationService: GeolocalizationService, private cryptoTool: CryptoTool) { }

  ngOnInit() {
  }

  ngAfterViewInit() {
    //this.loadCheckpoints();
    this.loadCheckpointsByAccount();
  }

  loadCheckpoints() {
    let routeParam = this.route.snapshot.paramMap.get('route');
    this.geolocalizationService.getCheckpointsByRoute(routeParam)
    .subscribe(data => {
      this.vehicles = data;
      console.log("Load Checkpoints success! " );
    })
  }

  loadCheckpointsByAccount() {
    const account = this.cryptoTool.decryptString(localStorage.getItem('vp'));
    //let routeParam = this.route.snapshot.paramMap.get('route');
    this.geolocalizationService.getCheckpointsByAccount(account)
    .subscribe(data => {
      for (let point of data) {
        this.checkpoints.push(point.last_point)
      }
      this.vehicles = [{route:"", fleet:"", checkpoints:this.checkpoints, type:"1"}];
      console.log("Load Checkpoints success! " );
    })
  }

}
