import { Component, OnInit, Inject } from '@angular/core';
import { FormBuilder, FormControl, FormArray, FormGroup, Validators } from '@angular/forms';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { Vehicle } from '../../interfaces/vehicle';

@Component({
  selector: 'add-vehicle',
  templateUrl: './add-vehicle.component.html',
  styleUrls: ['./add-vehicle.component.scss']
})
export class AddVehicleComponent implements OnInit {

  addVehicleForm;

  constructor(private formBuilder: FormBuilder, public dialogRef: MatDialogRef<AddVehicleComponent>,
    @Inject(MAT_DIALOG_DATA) public data: Vehicle) {
    this.addVehicleForm = this.formBuilder.group({
      code: new FormControl('', [

      ]),
      brand: new FormControl('', [

      ]),
      model: new FormControl('', [

      ]),
      year: new FormControl('', [

      ]),
      type: new FormControl('', [

      ])
    });
  }

  ngOnInit() {
  }

  addVehicle() {
    console.log("raw value");
    console.log(this.addVehicleForm.getRawValue());
    this.dialogRef.close(this.addVehicleForm.getRawValue());
  }

  // If the user clicks the cancel button a.k.a. the go back button, then\
  // just close the modal
  close() {
    this.dialogRef.close();
  }

  get code() { return this.addVehicleForm.get('vehicle').get('code'); }
  get brand() { return this.addVehicleForm.get('vehicle').get('brand'); }
  get model() { return this.addVehicleForm.get('vehicle').get('model'); }
  get year() { return this.addVehicleForm.get('vehicle').get('year'); }
  get type() { return this.addVehicleForm.get('vehicle').get('type'); }

}
