import { Component, OnInit, AfterViewInit, ViewChild } from '@angular/core';
import {MatPaginator} from '@angular/material/paginator';
import {MatTableDataSource} from '@angular/material/table';
import {MatSort} from '@angular/material/sort';
/* Services */
import { AccountService } from '../services/account.service';
import { Track } from '../interfaces/track';
import { CryptoTool } from '../../core/crypto/crypto-tool'

@Component({
  selector: 'track-list',
  templateUrl: './track-list.component.html',
  styleUrls: ['./track-list.component.scss']
})
export class TrackListComponent implements AfterViewInit {

  @ViewChild(MatPaginator, {static: true}) paginator: MatPaginator;
  @ViewChild(MatSort, {static: false}) sort: MatSort;
  //displayedColumns: string[] = ['fleet', 'route', 'status', 'vehicles', 'play'];
  data: any;
  resultsLength = 0;
  trackDescription: string = ""
  trackList: Track[] = [];
  //columns: [] = [];
  displayedColumns : string [] = [];
  columns : object [] = [];
  tableData : string [] = [];

  constructor(private accountService: AccountService, private cryptoTool: CryptoTool) { }

  ngOnInit() {
  }

  ngAfterViewInit() {
    //this.updateTableData();
    this.loadAccount();
  }

  loadAccount() {
    const account = localStorage.getItem('vp')
    this.accountService.getAccountDetailsByAccount(this.cryptoTool.decryptString(account))
    .subscribe(data => {
      const headers = data.service.settings.tables.parameters.table.headers;
      //this.displayedColumns = data.service.settings.tables.parameters.table.headers;
      this.displayedColumns = this.generateHeaders(headers);
      //this.trackDescription = data.description;
      this.columns = this.generateColumns(headers);
      this.tableData = this.generateData(headers, data);
      //this.generateColumns(data.service.settings.tables.parameters.table);
      this.generateDataToTable(data);
      //this.data = new MatTableDataSource<Track>(this.trackList);
      this.data = new MatTableDataSource<String>(this.tableData);
      this.data.paginator = this.paginator;
      console.log("Load Accounts success! " );
    });
  }

  generateDataToTable(matrixData: any){
    matrixData.matrix.forEach(matrix => {
      matrix.routes.forEach(route => {
        const track = {
          matrix: matrix.name,
          route: route.name,
          status: route.status,
          operators: route.operators.length,
          account: matrixData.account,
          routeId: route.id
        };
        this.trackList.push(track);
      });
    });
  }

  /*generateColumns(tables: any){
    tables.headers.forEach(item => {
      this.columns.push({ columnDef: item.header, header: item.header, cell: (element: Element) => `${element.position}` })
    });
    this.displayedColumns = this.columns.map(c => c.columnDef);
  }*/

  updateTableData(){
    //this.displayedColumns = this.generateHeaders(this.columnSliderValue);
    //this.columns = this.generateColumns(this.columnSliderValue);
    //this.tableData = this.generateData(this.columnSliderValue, this.rowSliderValue);

    //this._dataSubject.next(this.tableData)
  }

  // Create headers, this is just a array of strings
  generateHeaders(tableColumns : any){
    var innerIndex : number = 0;
    var displayedColumns : string [] = [];

    do{
      displayedColumns.push(tableColumns[innerIndex])
    }
    while (innerIndex++ < tableColumns.length);

    return displayedColumns;

  }

  // Create columns, this is an array of objects. The object the holds the headingName, Label and Cell
  generateColumns(tableColumns : any){
    var innerIndex : number = 0;
    var columnObj : object;
    var columns : object[] = [];

    do{
      columnObj = new function(){
        this.columnDef = tableColumns[innerIndex];
        this.header    = tableColumns[innerIndex];
        this.cell      = [];
      }
      columns.push(columnObj)
    }

    while (innerIndex++ < tableColumns.length);

    return columns;

  }

  // Create table data, this is just a 2d array of sequential numbers
  generateData (tableColumns : any, tableRows : any){
    var innerIndex : number = 0;
    var outerIndex : number = 0;
    var value : number;
    var tableRow : string [] = [];
    var tableData : any [] = [];

    /*do{
      innerIndex = 1;
      do{
        value = outerIndex * 10 + innerIndex
        tableRow.push(value.toString())
      }
      while (innerIndex++ < tableColumns.length);

      tableData.push(tableRow);
      tableRow = [];

    }
    while (outerIndex++ < tableRows.length - 1);*/
    tableRows.matrix.forEach(matrix => {
      matrix.routes.forEach(route => {
        const track = [
          matrix.name,
          route.name,
          route.operators.length,
          route.status,
          tableRows.account,
          route.code
        ];
        tableData.push(track);
      });
    });

    return tableData;

  }


}
